export default {
    data() {
        return {
            email: '',
        }
    },
    methods: {
        async submitForm() {
            await this.$axios.$post('/api/maylday/auth/code', {
                "email": this.email
            })
            .then((response) => {
                console.log(response);
                this.$router.push('/log-in/code?email=' + encodeURIComponent(this.email))
            }, (error) => {
                console.log(error);
                //this.res = error.response.data.message
            });
        }
    },
}
